.navbar {
  width: 100%;
  min-width: 1000px;
  display: flex;
  
  position: fixed;
  z-index: 100;
}
.content {
  display: block;
  position: relative;
  top: 56px;
}
.slide {
  padding-left: 80px;
  margin: 0px;
}
.start {
  opacity: 0;
}
.end {
  opacity: 1;
  transition: opacity 0.5s;
}
.main-bg {
  position: relative;
  display: block;
  width: 100vw;
  min-width: 900px;
  height: 300px;
  /* background-image: url('./img/bg.png'); */
  background-size: cover;
  background-position: center;
  transition: width 0.5s;
}
.scroll {
  position: relative;
  overflow: hidden;
}
.row {
  position: relative;
  width: 900px;
}
.App {
  background-color: white;
  text-align: center;
  letter-spacing: -0.8px;
}


